.footer {
  background-color: $main-background-color;
}
.address {
  font-style: normal;
  text-align: right;
}
.footer__flex-container {
  @media screen and (min-width: 768px) {
    display: flex;
    margin-bottom: 120px;
    justify-content: space-between;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 97px;
  }
}
.footer__title {
  font-family: $font-family-heading;
  text-transform: uppercase;
  color: $secondary-color;
    margin-bottom: 17px;
    font-size: 18px;
    line-height: 1.16;
    font-weight: 400;  
  width: 234px;
  @media screen and (min-width: 768px) {
   width: 340px;
    margin-bottom: 16px;
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: 1280px) {
    width: 480px;
    font-size: 30px;
    line-height: 1.13;
    margin-bottom: 15px;
  }
}
.footer__title--lower {
  font-family: $font-family-heading;
  text-transform: uppercase;
  color: $accent-color;  
   width: 200px;
    margin-bottom: 43px;
    font-size: 34px;
    line-height: 1.14;
    letter-spacing: 0.06em;
  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.14;
    margin-bottom: 0;
  }
  @media screen and (min-width: 1280px) {
    width: 500px;
    font-size: 58px;
    line-height: 1.14;
  }
}
.footer__title--container {
  
  @media screen and (min-width: 768px) {
    width: 350px;
  }
  @media screen and (min-width: 1280px) {
    width: 475px;
  }
}
.footer__contact {
   margin-bottom: 65px;
    text-align: right;
}
.social {
  display: flex;
    justify-content: flex-end;
  @media screen and (max-width: 1279px) {
    margin-bottom: 46px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 50px;
  }
}
.social__item {
  width: 34px;
  height: 34px;
  background-image: radial-gradient($icon-social-media, $icon-social-media);
  border-radius: 360px;
    margin-left: 28px;
  @media screen and (min-width: 768px) {
    margin-left: 30px;
  }
  @media screen and (min-width: 1280px) {
    margin-left: 24px;
  }
}
.social__link {
  color: $button-text;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: $transition;
  &:hover,
  &:focus {
    background-color: $accent-color;
  }
}
.social__icon {
  width: 20px;
  height: 20px;
  fill: currentColor;
}
.footer__adress, .footer__adress--phone {
  font-family: $main-font;
  font-size: 12px;
  font-weight: 700;
  color: $main-text-color;
  line-height: 1.83;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.85;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.87;
  }
}
.footer__adress--phone {
  color: $accent-color;
}
.footer__rights {
  font-family: $main-font;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
  color: $secondary-text-color;
  margin: auto; 
   width: 150px;
   padding-bottom: 25px;
    &::before {
      position: absolute;
      left: 0;
      margin-top: -22px;
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      background-color: $background-slider; 
  }
  @media screen and (min-width: 768px) {
    letter-spacing: 0.02em;
    text-align: center;
    width: 100%;
    padding-bottom: 18px;
    &::before {
      margin-top: -29px;
    }
  }
  @media screen and (min-width: 1280px) {
    font-size: 14px;
    padding-bottom: 10px;
    &::before {
      margin-top: -9px;
    }
  }
}
.button-home{
  position: fixed;
  z-index: 100;
  width: 40px;
  height: 40px;
  border:1px solid $accent-color;
  border-radius: 50%;
  transform: rotateZ(270deg);
  @media screen and(max-width:767px){
    right: 20px;
    bottom: 25px;
  }
  @media screen and(min-width:768px) and(max-width:1279px){
    right:36px;
    bottom: 10px;
  } 
  @media screen and(min-width:1280px) {
    right: 200px;
    bottom: 2px;
    width: 35px;
  height: 35px;
  }
}
